@import "./Vars.scss";
@import "./Media.scss";
body {
    margin: 0;
    font-family: 'Muli', sans-serif;
    background: #8f57fd;
  }
  
  * {
    box-sizing: border-box;
  }

  .button {
    background-color: rgba(255, 255, 255, .3);
    border: none;
    border-radius: 25px;
    color: white;
    cursor: pointer;
    font-size: 16px;
    font-weight: bold;
    font-family: 'Muli', sans-serif;
    height: 50px;
    letter-spacing: 1px;
    margin: 10px 0;
  }